@import "abstracts/functions";
@import "abstracts/vars";

.top-categories {
	display: grid;
	grid-template-columns: repeat(8, 1fr);
	grid-gap: rem(10);

	@media(max-width: 1279px) {
		display: none;
	}
}

.mini-category {
	display: block;
	background-color: #b5d8fd;
	border-radius: rem(16);
	width: 100%;
	height: rem(76);
	position: relative;
	overflow: hidden;
	text-align: left;
	transition: background-color 0.2s ease;

	&__title {
		font-size: rem(14);
		line-height: 1.3;
		font-weight: bold;
		color: $colorBlueDark;
		position: absolute;
		top: rem(13);
		left: rem(9);
		z-index: 2;
		display: block;
		max-width: rem(90);
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		overflow: hidden;

		@media(min-width: 1367px) {
			font-size: rem(16);
		}
	}

	&__img {
		display: block;
		width: rem(82);
		height: rem(76);
		margin: 0 0 0 auto;
		position: relative;

		&::after {
			content: '';
			display: block;
			position: absolute;
			width: 100%;
			height: 100%;
			max-width: rem(300);
			top: 50%;
			left: 50%;
			z-index: 1;
			transform: translate(-50%, -50%);
			border-radius: 100%;
			background-color: rgba(#fff, 0.6);
			filter: blur(60px);
		}

		img {
			width: 100%;
			height: 100%;
			transform-origin: center;
			transition: transform 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);
			position: relative;
			z-index: 2;
		}
	}

	&:hover {
		background-color: #f6f4e7;

		img {
			transform: scale(1.05);
		}
	}

	&.active {
		background-color: #f6f4e7;

		img {
			transform: scale(1.05);
		}
	}

	&:nth-child(1) {

		.mini-category__title {
			max-width: 100%;
		}

		.mini-category__img {
			width: rem(54);
			height: rem(50);
			margin: rem(23) rem(10) rem(2) auto;
		}
	}

	&:nth-last-child(3) {
		.mini-category__img {
			transform: scale(0.85) translateX(9px);
		}
	}
}

.mid-categories {
	display: grid;
	grid-template-columns: 1fr;
	grid-gap: rem(16);

	@media(min-width: 745px) {
		grid-gap: rem(23);
	}

	@media(min-width: 992px) {
		grid-template-columns: rem(360) 1fr;
		grid-gap: rem(10);
	}

	@media(min-width: 1320px) {
		grid-template-columns: rem(414) 1fr;
		grid-gap: rem(20);
	}

	+ .mid-categories {
		margin: rem(20) 0 0;

		@media(min-width: 992px) {
			margin: rem(11) 0 0;
		}
	}

	&:nth-child(3) {
		.big-category__img {
			height: 55%;
			margin: 0 rem(11) rem(6) auto;

			@media(min-width: 745px) {
				margin: 0 rem(35) rem(13) auto;
			}

			@media(min-width: 992px) {
				margin: 0 rem(10) rem(10) auto;
			}

			@media(min-width: 1320px) {
				height: 59%;
				margin: 0 rem(24) rem(6) auto;
			}
		}

		.mid-category__img {
			@media(max-width: 991px) {
				height: rem(100);
				margin: 0 rem(10) rem(6) 0;
			}
		}

	}

	&:nth-child(1),&:nth-child(2) {

		.big-category__img {
			position: relative;
			height: 93%;
			margin: 0 rem(21) 0 auto;

			@media(min-width: 745px) {
				margin: 0 rem(57) 0 auto;
			}

			@media(min-width: 992px) {
				height: 74%;
				margin: 0 rem(14) 0 auto;
			}

			@media(min-width: 1320px) {
				margin: 0 rem(46) 0 auto;
				height: 90%;
			}

			&::after {
				content: '';
				display: block;
				position: absolute;
				width: 100%;
				height: 100%;
				max-width: rem(127);
				top: 50%;
				right: 16%;
				z-index: 1;
				transform: translateY(-50%);
				border-radius: 100%;
				background-color: #fff;
				filter: blur(30px);

				@media(max-width: 991px) {
					max-width: rem(127);
					right: 1%;
				}
			}

			img {
				position: relative;
				z-index: 2;
			}
		}

	}

	&:nth-child(2) {
		.mid-category:nth-child(5) {
			.mid-category__img {
				margin: 0;
			}
		}
	}

	&__list {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: rem(11);
		transition: margin-bottom 0.2s ease;
		margin: 0 0 rem(5);

		@media(min-width: 745px) {
			grid-template-columns: repeat(4, 1fr);
		}

		@media(min-width: 992px) {
			grid-template-columns: repeat(3, 1fr);
			margin: 0;
		}

		@media(min-width: 1320px) {
			grid-template-columns: repeat(4, 1fr);
			grid-gap: rem(7) rem(20);
		}

		.mid-category {

			&:nth-child(n+5) {
				height: 0;
				opacity: 0;
				overflow: hidden;

				.mid-category__title {
					opacity: 0;
				}
			}

			@media(min-width: 992px) and (max-width: 1319px) {
				&:nth-child(n+4) {
					height: 0;
					opacity: 0;
					overflow: hidden;

					.mid-category__title {
						opacity: 0;
					}
				}
			}
		}

		&.open {
			margin-bottom: rem(12);

			@media(min-width: 992px) {
				margin-bottom: rem(7);
			}

			.mid-category {

				&:nth-child(n+5) {
					height: rem(196);
					opacity: 1;

					.mid-category__title {
						opacity: 1;
						transition: opacity 0.2s 0.2s ease;
					}

					@media(max-width: 991px) {
						height: rem(156);
					}
				}

				@media(min-width: 992px) and (max-width: 1319px) {
					height: rem(196);
					opacity: 1;

					.mid-category__title {
						opacity: 1;
						transition: opacity 0.2s 0.2s ease;
					}
				}
			}
		}
	}
}

.big-category {
	display: flex;
	align-items: flex-end;
	background-color: #b5d8fd;
	border-radius: rem(16);
	width: 100%;
	height: rem(126);
	position: relative;
	overflow: hidden;
	transition: background-color 0.2s ease;

	@media(min-width: 745px) {
		height: rem(216);
	}

	@media(min-width: 992px) {
		height: rem(196);
	}


	&__body {
		position: absolute;
		top: rem(6);
		left: rem(8);
		z-index: 1;
		display: block;
		max-width: 75%;

		@media(min-width: 745px) {
			top: rem(12);
			left: rem(21);
		}

		@media(min-width: 992px) {
			top: rem(20);
			left: rem(15);
		}
	}

	&__title {
		font-size: rem(36);
		line-height: 1.3;
		font-weight: bold;
		color: $colorBlueDark;

		@media(min-width: 745px) and (max-width: 991px) {
			font-size: rem(68);
		}
	}

	&__sub {
		font-size: rem(20);
		margin: rem(-6) 0 0;
		font-weight: 300;
		color: $colorBlueDark;

		@media(min-width: 745px) and (max-width: 991px) {
			font-size: rem(40);
			margin: rem(-16) 0 0;
		}
	}

	&__img {
		display: flex;
		align-items: flex-end;
		justify-content: flex-end;
		width: 60%;
		margin: 0 0 0 auto;

		img {
			width: auto;
			height: 100%;
			transform-origin: center;
			transition: transform 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);
		}
	}

	&:hover {
		background-color: #f6f4e7;

		img {
			transform: scale(1.05);
		}
	}
}

.mid-category {
	display: flex;
	justify-content: flex-end;
	background-color: #eaf4ff;
	border-radius: rem(14);
	height: rem(156);
	width: 100%;
	position: relative;
	overflow: hidden;
	transition-property: background-color, height, opacity;
	transition: 0.2s ease;

	@media(min-width: 992px) {
		height: rem(196);
	}

	&__title {
		max-width: 80%;
		position: absolute;
		top: rem(10);
		left: rem(10);
		z-index: 1;
		font-family: $secFont;
		font-size: rem(16);
		line-height: 1.2;
		color: $colorBlueDark;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		overflow: hidden;
		transition: color 0.2s ease;

		@media(min-width: 992px) {
			top: rem(20);
			left: rem(20);
		}
	}

	&__img {
		display: flex;
		align-items: flex-end;
		align-self: flex-end;
		width: rem(120);
		height: rem(120);
		margin: 0 rem(7) 0 0;

		@media(min-width: 992px) {
			width: rem(180);
			height: rem(150);
			margin: 0;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			transform-origin: center;
			transition: transform 0.2s cubic-bezier(0.25, 0.1, 0.25, 1);
		}
	}

	&:hover {
		background-color: #f6f4e7;

		.mid-category__title {
			color: $colorBlueLight;
		}

		img {
			transform: scale(1.05);
		}
	}
}

.home-advantages {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: rem(24);

	@media(min-width: 1200px) {
		grid-gap: rem(33);
	}
}

.adv-item {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	gap: rem(10);
	max-width: rem(208);
	margin: 0 auto;

	@media(min-width: 992px) {
		gap: rem(30);
		flex-direction: row;
		max-width: rem(292);
	}

	&__icon {
		display: flex;
		justify-content: center;
		align-items: center;
		width: rem(64);
		height: rem(64);

		@media(min-width: 745px) and (max-width: 991px)  {
			width: rem(100);
			height: rem(100);
		}

		img {
			width: 100%;
			height: 100%;
		}
	}

	&__body {
		margin: 0 auto;
		text-align: center;
		font-size: rem(12);
		line-height: 1.4;
		color: $colorBlueDark;

		@media(min-width: 745px) {
			font-size: rem(16);
			line-height: 1.8;
		}

		span {
			display: block;
			font-size: rem(14);
			font-weight: bold;
			margin: rem(0) 0 0;

			@media(min-width: 745px) {
				font-size: rem(20);
				margin: rem(-4) 0 0;
			}
		}
	}
}


.home-slider {
	width: 100%;
	max-width: rem(1200);
	margin: 0 auto;
	padding: 0 0 rem(34);
	position: relative;

	.swiper-pagination {
		bottom: 0;
	}

	.swiper-btn {
		position: absolute;
		top: 48%;
		z-index: 2;
		transform: translateY(-50%);

		&.js-next-swiper {
			right: rem(-24);
		}

		&.js-prev-swiper {
			left: rem(-24);
		}

		@media(max-width: 991px) {
			display: none;
		}
	}
}


.home-slide {
	display: block;
	width: 100%;
	height: rem(328);
	border-radius: rem(30);
	position: relative;
	//border: 1px solid rgba(#404040, 0.6);
	overflow: hidden;

	@media(min-width: 680px) {
		height: rem(660);
	}

	@media(min-width: 992px) {
		height: rem(480);
	}

	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		width: 100%;
		height: 100%;
		background-color: rgba(#fff, 0.5);
		border-radius: rem(30);
		transition: background-color 0.2s ease;
	}

	&__title {
		position: absolute;
		bottom: rem(52);
		left: rem(15);
		z-index: 2;

		@media(min-width: 680px) {
			bottom: rem(109);
			left: rem(19);
		}

		@media(min-width: 992px) {
			bottom: rem(70);
			left: rem(36);
		}

		span {
			display: block;
			line-height: 1;
			color: $colorBlueDark;

			&:nth-child(1) {
				font-size: rem(40);
				font-weight: bold;

				@media(min-width: 680px) {
					font-size: rem(60);
				}
			}

			&:nth-child(2) {
				font-size: rem(24);

				@media(min-width: 680px) {
					font-size: rem(36);
				}

				@media(min-width: 680px) and (max-width: 991px) {
					margin: rem(12) 0;
				}
			}

			&:nth-child(3) {
				font-size: rem(24);
				font-weight: bold;
				text-transform: uppercase;
				font-style: italic;
				margin: 0 0 0 rem(-13);

				@media(min-width: 680px) {
					font-size: rem(46);
				}

				@media(min-width: 992px) {
					margin: rem(-1) 0 0 rem(-23);
				}
			}
		}
	}

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: top;
		border-radius: rem(30);
	}

	&:hover {

		&::after {
			background-color: rgba(#fff, 0.3);
		}
	}
}

